<template>
  <div id="app">
    <div class="board">
      <app-row v-for="(row, index) in rows" :key="index" :row="row"></app-row>
    </div>
    <app-log></app-log>
  </div>
</template>

<script>
import Row from "./components/Row.vue";
import Log from "./components/Log.vue";
export default {
  name: "app",
  data() {
    return {
      rows: []
    };
  },
  components: {
    appRow: Row,
    appLog: Log
  },
  created() {
    let columns = ["a", "b", "c", "d", "e", "f", "g", "h"];
    for (let i = 8; i > 0; i--) {
      let row = [];
      for (let j = 0; j < columns.length; j++) {
        let square = {
          row: i,
          column: columns[j]
        };
        row.push(square);
      }
      this.rows.push(row);
    }
  }
};
</script>

<style lang="scss">
body,
html {
  height: 100%;
  width: 100%;
  font-family: sans-serif;
  font-weight: bold;
  margin: 0;
  padding: 0;
}
#app {
  display: flex;
  height: 100%;
  width: 100%;
  margin: auto;
  max-width: 1200px;
  @media (orientation: portrait) {
    flex-direction: column;
  }
}
.log {
  margin: auto;
  flex: 1;
  background: #f5f5f5;
  padding-left: 3rem;
  height: calc(95vmin + 1rem);
  width: auto;
  @media (orientation: landscape) {
    overflow-y: auto;
  }
  @media (orientation: portrait) {
    height: auto;
    width: calc(95vmin + 1rem);
  }
}
.board {
  margin: auto;
  height: 95vmin;
  width: 95vmin;
  padding: 0.5rem;
  background: black;
  .row {
    width: 100%;
    height: calc(95vmin / 8);
    display: flex;
    &:nth-of-type(odd) {
      div:nth-of-type(odd) {
        background: black;
      }
      div:nth-of-type(even) {
        background: white;
      }
    }
    &:nth-of-type(even) {
      div:nth-of-type(even) {
        background: black;
      }
      div:nth-of-type(odd) {
        background: white;
      }
    }
    > div {
      flex: 1;
      cursor: pointer;
      &.focus {
        background: rgb(116, 168, 221) !important;
      }
    }
  }
}
</style>



